.single-email{
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(128, 128, 128, 0.11);
  transition: 0.1s;
  cursor: pointer;
  position: relative;
}
.single-email .info{
  display: flex;
  align-items: center;

}
.single-email .info p{
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.single-email .info .from{
  margin: 10px;
  min-width: 150px;
  width: 200px;
  font-weight: bold;
}
.single-email .info .title{
  font-weight: bold;
  max-width: 150px;
  margin-right: 5px;
}
.single-email .info .body{
  width: 610px;
}
.single-email .info .date{
  position: absolute;
  top: 10px;
  right: 50px;
}
.single-email:hover{
  box-shadow: 1px 1px 6px #ccc;
  transform: scale(1.01);
}