.mail-section{
  flex: 0.82;
  margin: 10px;
  border: 1px solid whitesmoke;
  box-shadow: 1px 1px 8px rgba(128, 128, 128, 0.096);
  min-height: 80vh;
}
.top-widgets{
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
}
.mail-section .container{
  padding: 20px;
  background: rgba(128, 128, 128, 0.116);
  height: 100%;
}
.mail-section .container .content{
  background: white;
  height: 100%;
  box-shadow: 1px 1px 8px #ccc;
  padding: 20px;
}
.mail-section .container .content .header{
  display: flex;
  position: relative;
  margin-left: 20px;
}
.mail-section .container .content .header .important{
  color: rgb(255, 196, 0) !important;
  margin: 0 20px;
}
.mail-section .container .content .header .date{
  position: absolute;
  right: 0;
  color: gray;
}

.mail-section .container .content .message{
  margin-top: 20px;
}