.send-mail{
  position: fixed;
  right: 10px;
  bottom: 0;
  width: 400px;
  min-height: 285px;
  box-shadow: 1px 1px 8px gray;
  border-radius: 10px 10px 0 0;
  background: white;
}
.send-mail >span{
  position: absolute;
  top: 9px;
  right: 15px;
  color: white;
  cursor: pointer;
}
.send-mail >p {
  padding: 10px;
  border-radius: 10px 10px 0 0;
  background: rgb(46, 46, 48);
  color: white;
  font-size: 14px;
}
.send-mail form{
  display: flex;
  flex-direction: column;

}
.send-mail form input{
  border: none;
  padding: 10px;
}
.send-mail form input:last-of-type{
/* height: 100px; */
}
.send-mail form textarea{
  border: none;
  resize: none;
  padding: 10px;
  height: 130px;
}
.send-mail form >div{
  background: rgb(46, 46, 48);
  padding: 10px;
}
.send-mail form >div button{
  padding: 5px 10px;
  background: rgb(82, 121, 247);
  color: white;
  border: none;
  cursor: pointer;
}
.error{
  color: red;
}