.right-option{
  border-radius: 10px;
  display: flex;
  align-items: center;
  margin-top: 10px;
  margin-left: 10px;
  color: gray;
  padding: 5px;
  justify-content: space-between;
}
.right-option div{
  display: flex;
}
.right-option div p{
  margin-left: 10px;
  padding-right: 15px;
}
.right-option .number{
  display: none;
}
.right-option:hover{
  color: rgba(255, 0, 0, 0.507);
  background: rgba(255, 0, 0, 0.055);
  font-weight: bold;
}
.right-option:hover .number{
  display: block;
}
.active{
  color: rgba(255, 0, 0, 0.507);
  background: rgba(255, 0, 0, 0.055);
  font-weight: bold;
}
.active .number{
  display: block;
}