header {
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.041);
}
header .left{
  display: flex;
}
header .left .logo{
  display: flex;
  align-items: center;
  margin-left: 5px;
}
header .left .logo img{
  width: 30px;
  height: 30px;
  object-fit: contain;

}
header .left .logo p{
  margin-bottom: 5px;
  margin-left: 5px;
  color: gray;
}
header .middle{
  background: rgba(128, 128, 128, 0.096);
  color: rgb(107, 106, 106);
  position: relative;
  flex: 0.7;
}
header .middle input {
  width: 100%;
  height: 100%;
  background-color: inherit;
  border: none;
  padding-left: 40px;
  padding-right: 30px;
  outline: none;
}
header .middle  svg{
  position: absolute;
  pointer-events: none;
  top: 50%;
  transform: translate(5px,-50%);
}
header .middle  svg:last-of-type{
  right: 5px;
}
header .right{
  display: flex;
  align-items: center;
}
header .right img{
  margin: 5px 10px 0;
  border-radius: 50%;
  width: 35px;
}